<template>
    <b-container>
        <b-row>
            <b-col sm="12" class="mt-2 mb-2"><center><h1>DEPOIMENTOS</h1></center></b-col>
            <b-col sm="12">
                <h3 class="text-center text-sm-left">Patrcia Evelin S. Soares</h3>      
                <p><b-img :src="patricia" align-v="center" class="float-none float-sm-left mx-auto mr-sm-3 mb-2"></b-img>No h palavras suficientes para descrever a imensa gratido que tenho pelo Cursinho Focus e por cada um dos professores que nos ensinou, e tenho certeza que continuam ensinando, com muita sabedoria e dedicao. Sempre estudei em escola pblica e sabia que teria que batalhar muito para conseguir cursar o meu to sonhado curso de Direito. Quando falava que seria advogada, alguns parentes e amigos riam, no acreditavam na possibilidade, pois na regio que morava, nvel superior era apenas para os filhos de fazendeiros, atualmente as coisas mudaram muito por l, ainda bem, mas eu acreditava e estava disposta a enfrentar o que fosse para conseguir.</p>
                <p>Aos meus 15 anos vim para So Paulo (sou mineira), pois sabia que aqui seria mais fcil a tentativa. Assim, no 2 ano do Ensino Mdio, conheci a Prof Ana Slvia que ao ver a minha vontade de ingressar em uma universidade, me falou sobre o Cursinho Focus e assim possibilitou que participasse dessa grande turma, que continua crescendo. No cursinho eu aprendi muito, as aulas eram excelentes, pois os professores ensinavam com amor, digo com toda convico, eles estavam ali, assim como a Prof Ana Slvia, pois acreditavam na transformao que a educao pode operar na vida de cada um daqueles jovens. E  isso mesmo, o conhecimento muda a nossa vida, abre portas e concretiza sonhos.</p>
                <p>Em 2009 passei no vestibular da Universidade Presbiteriana Mackenzie para o curso de Direito. Aps, com a nota do Enem consegui bolsa de 100% pelo Prouni. Tenho certeza que as aulas do cursinho e as oficinas de redao foram fundamentais para ambas as conquistas. Em junho deste ano enfrentei e superei a to temida prova de ordem e em julho foi a minha formatura, foi maravilhoso dar esse presente aos meus pais. Atualmente sou advogada em uma empresa e pretendo fazer um mestrado, estudar sempre ser minha maior paixo e espero poder contribuir com a sociedade, ensinando. queles que esto comeando, eu digo: tenham foco, disciplina e aproveitem toda a estrutura que o cursinho oferece, pois com certeza sero vitoriosos em seus sonhos.</p>
                <p><b>Ao Cursinho Focus a minha gratido e o meu carinho, tenho muito orgulho de fazer parte desta histria. Patrcia Evelin S. Soares</b></p>
            </b-col>
            <br>
            <b-col sm="12">
                <div class="separa pt-2"></div>
                <h3 class="text-center text-sm-left">Caroline Candido Veroneze</h3>
                <p class="formacao"><b>Caroline Candido Veroneze, 23  Bacharel em Biblioteconomia e Cincia da Informao na UFSCar, So Carlos.</b></p>
                <p><b-img :src="caroline" class="float-none float-sm-left mx-auto mr-sm-3 mb-2"></b-img>Fiz cursinho no ano de 2008, quando estava no 3 ano do ensino mdio, e no sabia ao certo qual curso queria seguir. Para mim, o cursinho foi muito bom, no apenas nas disciplinas desenvolvidas, mas tambm nos espaos de convivncia, troca de experincias, no acompanhamento psicolgico e nos prprios depoimentos de ex alunos que visitavam o cursinho para compartilhar suas experincias. Com certeza valeu a pena!</p>
                <p>Decidi pelo curso de biblioteconomia e cincia da informao por ser apaixonada por livros e admirar imensamente o trabalho de algumas bibliotecrias que conheci. Meu 1 ano foi complicado, apesar de me adaptar bem  cidade, o curso em si estava em fase de transio com muitas trocas de professores e estudos para reorganizar a grade curricular. Quase desisti. Mas antes quis experimentar trabalhar na rea. Assim, consegui um estgio que, apesar de pagar pouco e ser em outra cidade, foi essencial para que eu decidisse continuar no curso. Amei a experincia e tive a certeza de que queria me formar em biblioteconomia.</p>
                <p>Hoje, trabalho na Diretoria de Ensino Regio de Jundia, ocupando o cargo de Analista Sociocultural, administro a biblioteca da Diretoria de Ensino e trabalho no Programa Sala de Leitura juntamente com vrios professores e professoras de escolas estaduais da regio.</p>
                <p><b>Muito obrigada a todos os envolvidos na Associao Paideia, vocs fizeram a diferena na minha vida e tenho certeza que continuam a fazer a diferena na vida de muitas pessoas!</b></p>
            </b-col>
            <b-col sm="12">
                <div class="separa pt-2"></div>
                <h3 class="text-center text-sm-left">Aline Fransozi</h3>
                <p class="formacao"><b>Aline Fransozi, 25  Cursando engenharia florestal na USP, Piracicaba.</b></p>
                <p><b-img :src="aline" class="float-none float-sm-left mx-auto mr-sm-3 mb-2"></b-img>Quando eu entrei no cursinho, eu ainda no tinha certeza sobre a carreira a seguir, mas eu queria estar preparada para quando chegasse o momento da deciso. Eu sempre estudei em escolas pblicas, onde infelizmente no h incentivo para pensarmos fora da caixa e acabamos nos limitando a algumas poucas opes que nem sempre traduzem o que desejamos. <b>Ao final de 2014, eu estarei formada no curso que eu queria, na melhor universidade do pas. Qualquer agradecimento  Associao Paidia  pouco pela mudana que foi provocada na minha vida.</b> </p>
                <p>Mais do que aulas preparatrias para o vestibular, encontrei no cursinho uma equipe pronta para me apresentar um mundo de possibilidades e me convencer que eu poderia querer qualquer coisa! Com a orientao pedaggica e visitas s universidades promovidas pelo cursinho, eu descobri a carreira que me realizaria profissionalmente: Engenharia Florestal. Ainda afobada com as descobertas e um tanto insegura das possibilidades, me inscrevi para diversos cursos no primeiro ano. Para o meu espanto (sim, eles acreditaram mais do que eu!), passei em trs universidades pblicas  UNICAMP, UNESP e UniABC. Fiquei muito feliz por perceber que era possvel, mas um pouco triste porque no passei justamente no curso que eu mais queria.</p>
                <p>Era o momento de tomar a deciso: fazer o curso que eu havia passado ou tentar mais uma vez o que eu realmente queria? Foi mais fcil passar no vestibular dessas trs universidades do que desistir das mesmas, mas eu decidi acreditar no meu sonho e na capacidade da equipe em me preparar. Um ano depois eu ingressei em Engenharia Florestal na USP. Ao final de 2014, eu estarei formada no curso que eu queria, na melhor universidade do pas. Qualquer agradecimento  Associao Paidia  pouco pela mudana que foi provocada na minha vida. Hoje, eu espero poder retribuir um pouquinho do que fizeram por mim dizendo apenas uma palavra queles que esto iniciando essa jornada: Acredite!</p>
            </b-col>
            <b-col sm="12">
                <div class="separa pt-2"></div>
