                <p><b-img :src="ana" class="float-none float-sm-left mx-auto mr-sm-3 mb-2"></b-img>Ns do Focus Vestibulares da Associao Paidia nutrimos a alegria de termos colaborado com os jovens de Louveira a realizarem o sonho de ingressar em uma universidade. Mais do que colaboradores nos tornamos agentes de uma mudana social e humana na vida daqueles que passaram pelo projeto.</p>
